import {
	userCar,
	carIcon,
	afterSale,
	delCar,
	setDefaultCar,
	singleCar
} from "../../../api/user.js"
export default {
	data() {
		return {
			carId: '', //从我的
			carIcon: carIcon,
			totalCar: 0,
			carList: [],
			afterSaleINfo: {
				car_info: {},
				records: []
			},
			currIndex: 0, // 当前的索引
			currCarId: '' // 当前的id
		}
	},
	onLoad(opts) {
		uni.hideShareMenu()
		this.carId = opts.id || ''
	},
	onShow() {
		this.carList = []
		this.totalCar = 0
		this.currIndex = 0
		this.afterSaleINfo.records = []
		this.afterSaleINfo.car_info = {}
		if (this.carId == '') {
			this.getUserCar()
		} else {
			this.getSigleCar(this.carId)
		}
	},
	methods: {
		// 获取车辆
		getUserCar() {
			uni.showLoading({
				title: '加载中...'
			})
			userCar({
				data: {
					page_no: '1',
					page_size: '5'
				}
			}).then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					let { record_count, user_cars } = res.data.result
					this.totalCar = record_count;
					this.carList = user_cars;
					if (user_cars.length != 0) {
						this.getAfterSale(this.carList[this.currIndex].id)
					}
				}
			})
		},
		// 获取单个车辆 
		getSigleCar() {
			uni.showLoading({
				title: '加载中...'
			})
			singleCar({
				data: {
					id: this.carId
				}
			}).then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					this.carList = [res.data.result.user_car]
					this.getAfterSale(res.data.result.user_car.id)
				}
			})
		},
		// 改变swiper
		changeSwiper(e) {
			let currIndex = e.detail.current
			if (currIndex == this.carList.length) {
				this.afterSaleINfo.records = []
				this.afterSaleINfo.car_info = {}
				return
			}
			this.currIndex = currIndex
			this.getAfterSale(this.carList[this.currIndex].id)
		},
		// 获取售后服务记录
		getAfterSale(id) {
			uni.showLoading({
				title: '处理中...'
			})
			afterSale({
				data: {
					id: id
				}
			}).then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					this.afterSaleINfo = res.data.result
				}
			})
		},
		// 设置默认车辆
		setDefaultCarHandel(id, index) {
			if (this.carList[index].is_default == 1) {
				return
			}
			let that = this;
			setDefaultCar({
				data: {
					id: id
				}
			}).then(res => {
				if (res.data.code == 0) {
					uni.showToast({
						title: "设置成功",
						icon: "none"
					})
					that.currIndex = 0
					that.getUserCar()
				}
			})
		},
		// 删除车辆
		delCarHandel(id, index) {
			if (this.carList[index].is_obd == 1) {
				this.$isNotionObd().then(()=>{
					uni.navigateTo({
						url: "/pages/user/car-butler/car-butler?carId=" + id
					})
				}) 
			}
			if (this.carList[index].is_obd != 1 && this.carList[index].is_ico != 1) {
				let that = this;
				uni.showModal({
					title: '删除提醒',
					content: "确定要删除此此辆车吗？",
					confirmColor: '#B87252',
					success(r) {
						if (r.confirm) {
							delCar({
								data: {
									id: id
								}
							}).then(res => {
								if (res.data.code == 0) {
									uni.showToast({
										title: "删除成功",
										icon: "none"
									})
									that.currIndex = 0
									that.getUserCar()
								}
							})
						}
					}
				})
			}
		},
		openEditCar(id, isIco,carId,BrandId) {
			// isIco :1 4s店认证
			if (isIco == 1) {
				if(!carId || !BrandId){
					uni.navigateTo({
						url: '/pages/user/add-car/add-car?id=' + id
					})
				} else {
					uni.navigateTo({
						url: '/pages/user/edit-v-car/edit-v-car?id=' + id
					})
				}
				
			} else {
				uni.navigateTo({
					url: '/pages/user/add-car/add-car?id=' + id
				})
			}
		},
		// 打开添加车辆或者所有车辆
		openEditMore() {
			uni.navigateTo({
				url: `/pages/user/${this.totalCar >= 5 ? 'all-car' : 'add-car'}/${this.totalCar >= 5 ? 'all-car' : 'add-car'}`
			})
		},
		// 打开服务记录详情
		openPage(p, id) {
			uni.navigateTo({
				url: `/pages/user/${p}/${p}?id=${id}`
			})
		}
	}
}
